import React, { useState } from 'react';
import { useForm } from 'react-hook-form';
import { useAntContext } from '../utils/hooks';
import { login } from '../utils';

type LoginForm = {
  email: string;
  password: string;
};

type ServerError = {
  status?: number;
  msg: string;
};

const handleError = (e: any) => {
  if (e?.response?.statusText) {
    return {
      status: e.response.status,
      msg: e.response.statusText,
    };
  }
  if (e?.response) {
    return { msg: String(e.response) };
  }
  return { msg: 'Unkown Error - Try Again' };
};

export const Unauthenticated = () => {
  const { register, handleSubmit, errors } = useForm<LoginForm>();
  const { reset } = useAntContext();
  const [reqError, setReqError] = useState<ServerError | null>(null);
  const [loading, setLoading] = useState(false);
  const onSubmit = async (data: LoginForm) => {
    const { password, email } = data;
    setLoading(true);
    if (!password) {
      setReqError({ msg: 'Must Supply a Password!' });
      setLoading(false);
      return;
    }
    try {
      const { ip } = await login({ password, username: email });
      reset(ip, email);
    } catch (e) {
      setReqError(handleError(e));
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="form-group">
      <h1 style={{ textAlign: 'center', fontFamily: 'Bebas Neue, cursive' }}>THE ANT APP!</h1>
      {reqError && (
        <div className="errorBox">
          {reqError.status && <h3>{reqError.status}</h3>}
          <p>{reqError.msg}</p>
        </div>
      )}
      <form onSubmit={handleSubmit(onSubmit)}>
        <label>Email</label>
        <input
          type="text"
          id="email"
          ref={register({ required: true })}
          name="email"
          className="form-control"
        />
        {errors.email && errors.email.type === 'required' && <FormError />}
        <br />
        <label>
          Password <span>ü§´</span>
        </label>
        <input
          type="text"
          id="pass"
          ref={register({ required: true })}
          name="password"
          className="form-control"
        />
        {errors.password && errors.password.type === 'required' && <FormError />}
        <br />
        {loading ? <div className="loader" /> : <input type="submit" className="btn btn-primary" />}
      </form>
    </div>
  );
};

const FormError = () => (
  <div style={{ marginTop: 20 }}>
    <span>This is required ‚ùå</span>
  </div>
);
