import React, { createContext, useContext, useState } from 'react';

const Context = createContext<{
  user: string;
  email: string;
  reset: (u: string, e?: string) => void;
} | null>(null);

export const AntyContextProvider: React.FC = ({ children }) => {
  const [user, setUser] = useState('');
  const [email, setEmail] = useState('');
  const reset = (newUser: string, newEmail: string = '') => {
    setUser(newUser);
    setEmail(newEmail);
  };
  return <Context.Provider value={{ user, email, reset }}>{children}</Context.Provider>;
};

export const useAntContext = () => {
  const context = useContext(Context);
  if (!context) throw new Error('üêú, idk!');
  return context;
};
