import decode from 'jwt-decode';
import getIp from 'local-ip-url';
import config from '../config';
import request from './client';

export const login = async ({ username, password }: { username: string; password: string }) => {
  const ip = getIp('public', 'ipv4');
  const response = await request('post', '/login', { payload: { username, password } });
  const token = response?.data?.token;
  if (!token) {
    throw Error('Something Went Wrong!');
  }
  localStorage.setItem(config.tokenKey, token);
  return { ip, username };
};

export const logout = () => {
  console.log('logging out....');
  localStorage.removeItem(config.tokenKey);
};

export const isLoggedIn = async () => {
  const token = localStorage.getItem(config.tokenKey);
  const isTokenExpired = (token: string) => {
    try {
      const { expiration } = decode(token);
      return expiration < Date.now() / 1000;
    } catch {
      return false;
    }
  };
  return !!token && !isTokenExpired(token);
};

export const getFax = async () => {
  try {
    const token = localStorage.getItem(config.tokenKey) ?? '';
    const req = await request('get', '/fax', { token });
    if (req.status == 401) {
      logout();
      return null;
    }
    return req.data;
  } catch (e) {
    if (e?.response?.status == 401) {
      logout();
      return null;
    }
  }
};
