import axios, { AxiosRequestConfig } from 'axios';
import getIp from 'local-ip-url';
import config from '../config';

export type Params = { ips?: string[]; token?: string; payload?: any };
type Method = 'get' | 'post';

const createConfig = ({ ips, token, payload }: Params): AxiosRequestConfig => {
  const [priv, pub] = ips ?? [];
  const headers = token && { Authorization: `Bearer ${token}`, 'Content-Type': 'application/json' };
  const data = { ...payload, PRIV_IP: priv, PUB_IP: pub };
  return {
    baseURL: config.api,
    headers,
    data,
  };
};

const getBaseConfig = (): AxiosRequestConfig => {
  return { baseURL: config.api };
};

const withIpCapture = (f: typeof request) => {
  const ips = ['public', 'private'].map((t: any) => getIp(t, 'ipv4'));
  console.log('Public & Private Ips: ', ips.join(', '));
  return (method: Method, endpoint: string, params?: Params) => {
    return f(method, endpoint, { ...params, ips });
  };
};

export const request = (method: Method, endpoint: string, params?: Params) => {
  const config = params ? createConfig(params) : getBaseConfig();
  if (method === 'get') {
    return axios.get(endpoint, config);
  }
  // otherwise post
  const payload = config.data;
  return axios.post(endpoint, payload, config);
};

export default withIpCapture(request);
