import React, { useEffect, useState } from 'react';
import { getFax } from '../utils';
import { useAntContext } from '../utils/hooks';
import Toggle from 'react-toggle';

export const Home: React.FC = () => {
  const { user, reset } = useAntContext();
  const [fax, setFax] = useState<string[] | null>(null);
  const [money, setMoney] = useState(false);
  useEffect(() => {
    getFax()
      .catch(() => reset(''))
      .then(setFax);
  }, [money]);

  const addEmoji = (e: any) => {
    const [x, y] = [e.clientX, e.clientY];
    const child = document.createElement('span');
    child.innerText = money ? 'üí∏' : 'üêú';
    child.setAttribute('class', 'rain');
    child.style.left = x + 'px';
    child.style.top = y + 'px';
    document.body.appendChild(child);
  };

  useEffect(() => {
    window.addEventListener('click', addEmoji);
    return () => window.removeEventListener('click', addEmoji);
  }, [money, addEmoji]);

  return (
    <div style={{ backgroundColor: 'white', padding: 60 }}>
      <div style={styles.photo}>
        <div style={styles.mask} className="mask" />
      </div>
      <div style={styles.main}>
        <h1 style={styles.text}>
          {user ? `Welcome, IPv4-${user} ` : 'Ant App! '}
          <span role="img">üêú!!</span>
        </h1>
      </div>
      <h3>Money {money ? <span>‚úÖ</span> : <span>‚ùå</span>}</h3>
      <div style={{ marginTop: 20, marginBottom: 20 }}>
        <Toggle id="cheese-status" defaultChecked={money} onChange={() => setMoney(!money)} />
      </div>
      <h4>About</h4>
      {fax && (
        <ul>
          {fax.map((f, i) => (
            <li key={i}>{f}</li>
          ))}
        </ul>
      )}
      <div style={{}}>
        <div style={{ marginTop: 100, display: 'flex' }}>
          <div
            style={{
              transform: 'rotate(-15deg)',
              alignItems: 'center',
              margin: 'auto',
              marginRight: 0,
            }}
          >
            <img style={styles.dog} src="pit.png" placeholder="Wu Tang Forever" />
          </div>
          <div
            style={{
              transform: 'rotate(15deg)',
              alignItems: 'center',
              margin: 'auto',
            }}
          >
            <img style={styles.dog} src="dober.png" placeholder="Wu Tang Forever" />
          </div>
        </div>
      </div>
    </div>
  );
};

const styles = {
  main: {
    display: 'flex',
    alignItems: 'center',
  },
  text: {
    margin: 'auto',
  },
  wu: {
    display: 'flex',
    alignItems: 'center',
    margin: 'auto',
    width: 300,
    height: 300,
  },
  wuButton: {
    alignItems: 'center',
    margin: 60,
    marginLeft: 'auto',
    width: 180,
  },
  photo: {},
  mask: {
    width: 200,
    height: 200,
    borderRadius: 100,
    borderWidth: 2,
    borderStyle: 'solid',
  },
  dog: {
    width: 300,
    height: 300,
    borderRadius: 20,
    flex: 1,
  },
};
