import React, { useState, useEffect } from 'react';
import { Markets } from './pages/Markets';
import { Home } from './pages/Home';
import { Unauthenticated } from './pages/Unauthenticated';
import { UnauthenticatedLayout } from './components/UnauthenticatedLayout';
import { NavigationLayout } from './components/NavigationLayout';
import { BrowserRouter as Router, Switch, Route, Redirect } from 'react-router-dom';
import { isLoggedIn } from './utils';
import { AntyContextProvider, useAntContext } from './utils/hooks';
import { Unsupported } from './components/Unsupported';
import { isMobile, isIE } from 'react-device-detect';
import './App.css';

const App: React.FC = () => {
  return (
    <React.StrictMode>
      <Router>
        <AntyContextProvider>
          <Ant />
        </AntyContextProvider>
      </Router>
    </React.StrictMode>
  );
};

const Ant = () => {
  const { user } = useAntContext();
  const [auth, setAuth] = useState<boolean | undefined>();

  useEffect(() => {
    isLoggedIn().then(setAuth);
  }, [user]);

  // probably a better way to do this
  if (isMobile) {
    return (
      <UnauthenticatedLayout>
        <Unsupported type={'mobile'} />
      </UnauthenticatedLayout>
    );
  }

  if (isIE) {
    return (
      <UnauthenticatedLayout>
        <Unsupported type={'internet-explorer'} />
      </UnauthenticatedLayout>
    );
  }

  if (auth === undefined) {
    return null;
  }

  return auth ? (
    <NavigationLayout>
      <Switch>
        <Route exact path="/markets" component={Markets} />
        <Route exact path="/home" component={Home} />
        <Route path="*" render={() => <Home />} />
      </Switch>
    </NavigationLayout>
  ) : (
    <UnauthenticatedLayout>
      <Switch>
        <Route path="/login" component={Unauthenticated} />
        <Redirect from="*" to="/login" />
      </Switch>
    </UnauthenticatedLayout>
  );
};
export default App;
