import React from 'react';

const capitalize = (x: string) => {
  if (!x) {
    return x;
  }
  if (x.length <= 1) {
    return x.toUpperCase();
  }
  const capitalizer = (y: string) => {
    return y[0].toUpperCase() + y.slice(1);
  };
  return x.split('-').map(capitalizer).join(' ');
};

